import React, { PureComponent, createRef } from "react";
import { Modal, Form, Select, Input, InputNumber } from "antd";

const { Option } = Select;
const formItemLayout = {
	labelCol: { span: 4 },
	wrapperCol: { span: 20 },
};

export default class MyForm extends PureComponent {
	form = createRef();

	handleOk = e => {
		e.preventDefault();
		const { id } = this.props;
		this.form.current
			.validateFields()
			.then(values => {
				values.id =
					id ||
					Math.random()
						.toString(16)
						.substr(2, 6);
				this.props.onOk(values, () => this.form.current.resetFields());
			})
			.catch(e => console.log(e));
	};

	handleCancel = () => {
		this.props.onCancel();
	};

	render() {
		const { visible, onOk, onCancel, id, name, age, city, address } = this.props;

		console.log(this.props);

		return (
			<Modal
				title="用户信息"
				width="560px"
				visible={visible}
				onOk={this.handleOk}
				onCancel={this.handleCancel}
			>
				<Form
					{...formItemLayout}
					ref={this.form}
					name="userinfo"
					initialValues={{
						name,
						age,
						city,
						address,
					}}
					key={id}
				>
					<Form.Item
						label="姓名"
						name="name"
						rules={[
							{
								required: true,
								message: "请输入姓名",
							},
						]}
					>
						<Input placeholder="请输入姓名" autoComplete="off" />
					</Form.Item>
					<Form.Item
						label="年龄"
						name="age"
						rules={[
							{
								required: true,
								message: "请输入年龄",
							},
						]}
					>
						<InputNumber placeholder="请输入年龄" />
					</Form.Item>
					<Form.Item
						label="城市"
						name="city"
						rules={[
							{
								required: true,
								message: "请选择城市",
							},
						]}
					>
						<Select>
							<Select.Option value="New York">New York</Select.Option>
							<Select.Option value="London">London</Select.Option>
							<Select.Option value="Skeyney">Skeyney</Select.Option>
							<Select.Option value="Beijing">Beijing</Select.Option>
							<Select.Option value="Shanghai">Shanghai</Select.Option>
							<Select.Option value="Guangzhou">Guangzhou</Select.Option>
							<Select.Option value="Shenzhen">Shenzhen</Select.Option>
						</Select>
					</Form.Item>
					<Form.Item
						label="地址"
						name="address"
						rules={[
							{
								required: true,
								message: "请输入地址",
							},
						]}
					>
						<Input.TextArea rows={4} placeholder="请输入姓名" />
					</Form.Item>
				</Form>
			</Modal>
		);
	}
}
