import React, { Component } from "react";
import { Button, Select, Input, Table, Modal, message } from "antd";
import { PlusOutlined } from "@ant-design/icons";
import request from "@/components/request";
import { apis } from "@/config";
import Form from "./form";
import { getEmployeeList } from "@/models/employee";
import "./index.scss";

const { Option } = Select;
export default class Home extends Component {
	state = {
		edit: false,
		visible: false,
		data: [],
	};

	componentDidMount() {
		this.getEmployee();
	}

	getEmployee(params = {}) {
		getEmployeeList(params).then(data => {
			this.setState({
				data,
			});
		});
	}

	deleteEmployee = id => {
		request
			.post(apis.deleteEmployee, { id })
			.then(() => {
				this.getEmployee();
			})
			.catch(e => console.log(e));
	};

	addEmployee = data => {
		return request
			.post(apis.addEmployee, data)
			.then(() => {
				this.getEmployee();
			})
			.catch(e => {
				message.error(`新增失败`);
			});
	};

	editEmployee = data => {
		return request
			.post(apis.editEmployee, data)
			.then(() => {
				this.getEmployee();
			})
			.catch(e => console.log(e));
	};

	columns = [
		{
			title: "姓名",
			dataIndex: "name",
		},
		{
			title: "年龄",
			dataIndex: "age",
		},
		{
			title: "城市",
			dataIndex: "city",
		},
		{
			title: "地址",
			dataIndex: "address",
		},
		{
			title: "操作",
			dataIndex: "operator",
			render: (text, row) => {
				return (
					<>
						<a
							style={{ marginRight: 12, color: "#1890ff", cursor: "pointer" }}
							onClick={() => this.handleEdit({ ...row, edit: true })}
						>
							修改
						</a>
						<a
							style={{ color: "#ff4d4f", cursor: "pointer" }}
							onClick={() => this.handleDelete(row.id)}
						>
							删除
						</a>
					</>
				);
			},
		},
	];

	handleSeach = keywords => this.getEmployee({ keywords });

	handleEdit = data => {
		this.setState({ ...data, visible: true });
	};

	handleDelete = id => {
		Modal.confirm({
			title: "提示",
			content: "确定删除该条记录吗？",
			onOk: () => {
				this.deleteEmployee(id);
			},
		});
	};

	handleAdd = () => this.setState({ visible: true, edit: false });

	handleOk = (data, callback) => {
		this[this.state.edit ? "editEmployee" : "addEmployee"](data)
			.then(res => {
				this.handleCancel();
				callback();
			})
			.catch(e => {
				console.log(e);
			});
	};

	handleCancel = () =>
		this.setState({ visible: false, id: "", age: "", name: "", address: "", city: "" });

	render() {
		const { data, visible, name, age, city, address, id } = this.state;

		return (
			<div className="home-container">
				<div className="filter-wrapper">
					<Input.Search
						placeholder="姓名、年龄、城市、地址"
						style={{ width: 300 }}
						onSearch={this.handleSeach}
						enterButton
						allowClear
					></Input.Search>

					<Button
						type="link"
						icon={<PlusOutlined />}
						style={{ width: 150 }}
						onClick={this.handleAdd}
					>
						新增
					</Button>
				</div>
				<div className="main-table-wrapper">
					<Table columns={this.columns} dataSource={data} />
				</div>
				<Form
					id={id}
					visible={visible}
					name={name}
					age={age}
					city={city}
					address={address}
					onCancel={this.handleCancel}
					onOk={this.handleOk}
				/>
			</div>
		);
	}
}
